import { Base64 } from "js-base64";
import React, { useState } from "react";
import { useEffect } from "react";
import { Button, Container, Row } from "react-bootstrap";
import { FaPlay } from "react-icons/fa";

import ContentType from "./ContentType";
import Input from "./Input";

export default (props: {
    onOutputChanged: (output: string | undefined) => void;
}) => {
    const [content, setContent] = useState<string>('');
    const [isValid, setValid] = useState(true);
    const [type, setType] = useState<string>();
    const [format, setFormat] = useState<string>();
    const [output, setOutput] = useState<string>();

    const handleContentChanged = (content: string, isValid: boolean) => {
        setContent(content);
        setValid(isValid);
        setOutput(undefined);
    }

    const handleTypeChanged = (type: string, format: string) => {
        setType(type);
        setFormat(format);
    }

    const handleDecode = () => {
        if (isValid) {
            switch (type) {
                case 'text':
                    let bytes = Base64.toUint8Array(content);
                    let text = new TextDecoder(format).decode(bytes);
                    setOutput(text);
                    break;
                case 'image':
                    break;
                case 'hex':
                    break;
                case 'bin':
                    break;
                default:
                    console.log('Invalid format.', format)
            }

        } else {
            console.log('Invalid input. This should not happen.')
        }
    };

    useEffect(() => {
        props.onOutputChanged(output);
    }, [output]);

    return (
        <p>
            <Row>
                <Input type={type || 'text'} format={format || 'utf-8'} onContentChanged={handleContentChanged} />
            </Row>
            <Row>
                <ContentType onTypeChanged={handleTypeChanged} />
            </Row>
            <Row>
                <Container>
                    <Button variant="primary" disabled={!isValid} onClick={handleDecode}> <FaPlay />  Decode </Button>
                </Container>
            </Row>
        </p>
    );
}