import React, { useState, useCallback } from 'react';
import { Button, Container, Form, Row } from 'react-bootstrap';
import { useDropzone } from 'react-dropzone';
import { Base64 } from 'js-base64';

import { loadTextFile, selectFile } from '../utils/file-util';
import { useEffect } from 'react';
import { FaFile } from 'react-icons/fa';

export default (props: {
    type: string;
    format: string;
    onContentChanged: (content: string, isValid: boolean) => void;
}) => {
    const [content, setContent] = useState<string>('');
    const [isValid, setValid] = useState(true);

    const handleSelectFile = async () => {
        const file = await selectFile();
        if (file) {
            switch (props.type) {
                case 'text':
                    const content = await loadTextFile(file);
                    if (content) {
                        setContent(content);
                    }
                    break;
                case 'image':
                    break;
                case 'hex':
                    break;
                case 'bin':
                    break;
                default:
                    console.log('Invalid format.', props.type)
            }
        }
    }

    const handleFileDrop = useCallback(async acceptedFiles => {
        if (acceptedFiles && acceptedFiles[0]) {
            const content = await loadTextFile(acceptedFiles[0]);
            if (content) {
                setContent(content);
            }
        }
    }, [])
    const { getRootProps, isDragActive } = useDropzone({
        onDrop: handleFileDrop,
        noClick: true
    });

    useEffect(() => {
        try {
            let bytes = Base64.toUint8Array(content);
            let text = new TextDecoder(props.format).decode(bytes);
            setValid(true);
            props.onContentChanged(content, true);
        } catch (e) {
            console.log(e);
            setValid(false);
            props.onContentChanged(content, false);
        }
    }, [content])

    return (
        <Container>
            <Row>
                <p>
                    <Button variant="outline-dark" onClick={handleSelectFile}> <FaFile />  Load file </Button> or, paste the base64 encoded data, or drop the file in the text box below.
                </p>
            </Row>
            <Row>
                <p {...getRootProps()}>
                    <Form.Control
                        as="textarea"
                        isInvalid={!isValid && !isDragActive}
                        style={isDragActive ? { outline: '2px dotted' } : {}}
                        value={content}
                        onChange={e => setContent(e.target.value)}
                        rows={5}
                        placeholder="base64 content goes here." />
                </p>
            </Row>
        </Container>
    );
};