import FileSaver from "file-saver";
import React, { useState } from "react";
import { Button, Card, Container, Stack } from "react-bootstrap";
import { FaCopy, FaSave } from "react-icons/fa";

export default (props: {
    output: string;
}) => {
    const handleCopy = () => {
        navigator.clipboard.writeText(props.output);
    };

    const handleSave = () => {
        let blob = new Blob([props.output], { type: "text/plain;charset=utf-8" });
        FileSaver.saveAs(blob, "output.txt");
    };

    return (
        <Card>
            <Card.Header>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    Decoded output
                    <Stack direction='horizontal' gap={3} >
                        <Button variant="primary" onClick={handleCopy}> <FaCopy />  Copy to clipboard </Button>
                        <Button variant="primary" onClick={handleSave}> <FaSave />  Save as file </Button>
                    </Stack>
                </div>
            </Card.Header>
            <Card.Body>
                <Container>
                    <Card.Text>
                        {props.output}
                    </Card.Text>
                </Container>
            </Card.Body>
        </Card>
    );
}