import React, { Fragment, useState } from "react";
import { useEffect } from "react";
import { Stack, DropdownButton, Dropdown, ToggleButtonGroup, ToggleButton, Row, Container } from "react-bootstrap";

import ENCODINGS from '../assets/config/encodings.json';
import IMAGE_FORMATS from '../assets/config/image-formats.json';

export default (props: {
    onTypeChanged: (type: string, format: string) => void;
}) => {
    const TYPES = [
        {
            id: 'text',
            label: 'Text',
            jsx: (textEncoding: typeof ENCODINGS[0]) => (
                <Stack direction="horizontal" gap={3}>
                    <span style={{ display: 'inline-block' }}>Encoding:</span>
                    <DropdownButton id="dropdown-basic-button" variant="outline-dark" title={textEncoding.name} onSelect={handleTextEncodingClick} >
                        {
                            ENCODINGS.map(encoding => <Dropdown.Item key={encoding.code + '-' + encoding.name} eventKey={JSON.stringify(encoding)} >{encoding.name}</Dropdown.Item>)
                        }
                    </DropdownButton>
                </Stack >
            )
        },
        {
            id: 'image',
            label: 'Image',
            jsx: () => null
        },
        {
            id: 'hex',
            label: 'Hexadecimal',
            jsx: () => null
        },
        {
            id: 'bin',
            label: 'Binary',
            jsx: () => null
        }
    ];
    const [type, setType] = useState(TYPES[0]);

    const handleTextEncodingClick = (selection: any) => {
        const format = JSON.parse(selection);
        if (format) {
            setFormat(format);
        }
    }
    const [format, setFormat] = useState(ENCODINGS[0]);

    useEffect(() => {
        props.onTypeChanged(type.id, format.code);
    }, [type, format]);

    return (
        <Container>
            <Row>
                <p>
                    <Stack direction="horizontal" gap={3}>
                        <span style={{ paddingRight: '10px' }}>Content type:</span>
                        <ToggleButtonGroup type='radio' name='outputType' value={type.id}>
                            {TYPES.map(type => (
                                <ToggleButton key={type.id} variant="outline-dark" value={type.id} onClick={() => setType(type)}>{type.label}</ToggleButton>
                            ))}
                        </ToggleButtonGroup>
                    </Stack>
                </p>
            </Row>
            <Row>
                <p>
                    {type.jsx(format)}
                </p>
            </Row>
        </Container>
    );
};