import React from 'react';
import {
  Card,
  Col,
  Container,
  Nav,
  Row,
  Stack,
} from 'react-bootstrap'

import 'bootstrap/dist/css/bootstrap.min.css';
import '../styles/common.css'
import Header from '../components/Header';
import Notice from '../components/Notice';
import Decoding from '../components/Decoding';
import Output from '../components/Output';
import Footer from '../components/Footer';
import { useState } from 'react';

export default () => {
  const [output, setOutput] = useState<string>();

  const handleOutputChanged = (output: string | undefined) => {
    setOutput(output);
    console.log('output', output);
  };

  return (
    <Stack>
      <Header />
      <Container>
        <Row>
          <p>
            Base64 decoding and encoding tools with support for text, images and files - all with your data never leaving the browser.
          </p>
        </Row>
        <Row>
          <Col lg={9} sm={12}>
            <p>
              <Card>
                <Card.Header>
                  <Nav variant="tabs" defaultActiveKey="#decoding">
                    <Nav.Link href="#decoding">Decoding</Nav.Link>
                    <Nav.Link href="#encoding">Encoding</Nav.Link>
                  </Nav>
                </Card.Header>
                <Card.Body>
                  <Decoding onOutputChanged={handleOutputChanged} />
                </Card.Body>
              </Card>
            </p>
            <p>
              {output !== undefined ?
                <Output output={output} />
                :
                null
              }
            </p>
          </Col>
          <Col lg={3} sm={12}>
            <Notice />
          </Col>
        </Row>
        <Row>
          <Footer />
        </Row>
      </Container >
    </Stack >
  )
};
